:root{
  --bg-main: white;
  --text-sigma: black;
  --text-bright-text: rgb(103, 103, 103);
  --text-highlight-text: rgb(79, 79, 79);
}

[App-theme="dark"]{
  --bg-main: rgb(45, 45, 45);
  --text-sigma: rgb(255, 255, 255);
  --text-bright-text: rgb(125, 125, 125);
  --text-highlight-text: rgb(168, 168, 168);
}

/* Components */

/* main body */

.mainBody {
  background-color: var(--bg-main);
  background-image: url("./Assets/Images/BG.png");
  background-size: 15px;
  width: 100%;
  min-height: 100vh;
}


/* Toolbar */
.Tools {
  position: -webkit-sticky;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: fixed;
}

.Tools > div {
  background: rgba(154, 141, 175, 0.147);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  border: 2px solid rgba(52, 52, 52, 0.69);
  margin: 10px;
  padding: 20px;
}

/* Components */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
